import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import login from '@/components/login'
import registered from '@/components/registered'
import forgetPass from '@/components/forgetPass'
import userslist from '@/components/userslist'
import userInfo from '@/components/userInfo'
import header from '@/components/common/Header'
Vue.use(Router)

export default new Router({
    mode: "history",
    routes: [{
        path: '*',
        redirect: "/login"
    }, {
        path: '/login',
        name: 'login',
        component: login,
    }, {
        path: '/registered',
        name: 'registered',
        component: registered,
        meta: {
            title: "用户注册",
            index: "0"
        }
    }, {
        path: '/forgetPass',
        name: 'forgetPass',
        component: forgetPass,
        meta: {
            title: "忘记密码",
            index: "0"
        }
    }, {
        path: '/header',
        name: 'header',
        component: header,
        meta: {
            title: "登录后头部组件",
            index: "0"
        },
        children: [{
            path: '/index',
            name: 'index',
            component: index,
            meta: {
                title: "设备概览",
                index: "0"
            }
        }, {
            path: '/userslist',
            name: 'userslist',
            component: userslist,
            meta: {
                title: "用户管理",
                index: "1"
            }
        }, {
            path: '/userInfo',
            name: 'userInfo',
            component: userInfo,
            meta: {
                title: "账号设置",
                index: "1"
            }
        }],
    }, ]
})


